export default {
    name: 'app-exclusive-coupon',
    props: {
        receiveBg: {
            type: String,
            default: function() {
                return '';
            },
            required: false
        },
        list: {
            type: Array,
            default: function() {
                return [];
            },
            required: false
        },
        textColor: {
            type: String,
            default: function() {
                return  "#ffffff";
            },
            required: false
        },
        unclaimedBg: {
            type: String,
            default: function() {
                return '';
            },
            required: false
        },
        index: {
            type: Number,
            required: false
        },
        sign: {
            type: String,
            required: false
        },
        showTop: {
            type: Boolean,
            default() {
                return true;
            },
            required: false
        },
        noneColor: {
            type: Boolean,
            default() {
                return false;
            },
            required: false
        },
        background: {
            type: String,
            required: false
        },
        page_id: {
            type: Number,
            required: false
        },
        is_required: {
            type: Boolean,
            required: false
        },
        coupon_req: {
            type: Boolean,
            required: false
        }
    },
    data() {
        return {
            coupon_list: [],
            tempList: []
        };
    },
    computed: {
        receiveTip() {
            let receiveTip = '立即领取';
            if (this.sign === 'integral-mall') {
                receiveTip = '立即兑换';
            }
            return receiveTip;
        }
    },
    methods: {
        receive(index) {
            let list = this.coupon_list;
            if (this.sign === 'integral-mall') {
                this.$jump({
                    url: list[index].page_url,
                    open_type: 'navigate'
                });
                return ;
            }
            if (list[index].is_receive == 1) {
                uni.showToast({
                    mask: true,
                    title: '已领取',
                    icon: 'none'
                });
                return true;
            }
            uni.showLoading({
                mask: true,
                title: '领取中'
            });
            this.$request({
                url: this.$api.coupon.receive,
                data: {
                    coupon_id: list[index].id
                }
            }).then(e => {
                uni.hideLoading();
                if (e.code === 0) {
                    if (e.data.rest == 0) {
                        e.data.is_receive = 1;
                    }
                    this.coupon_list[index] = Object.assign(list[index], e.data);
                    let storage = this.$storage.getStorageSync('INDEX_MALL');
                    storage.home_pages[this.index].list = this.coupon_list;
                    this.$storage.setStorageSync('INDEX_MALL', storage);
                    this.$store.dispatch('page/actionSetCoupon', {
                        list: [this.coupon_list[index]],
                        type: 'receive'
                    });
                } else {
                    uni.showModal({
                        title: '提示',
                        content: e.msg,
                        showCancel: false,
                    });
                }
            }).catch(() => {
                uni.hideLoading();
            });
        },
        loadData() {
            this.$request({
                url: this.$api.index.extra,
                data: {
                    type: 'mall',
                    key: 'coupon',
                    page_id: this.page_id,
                    index: this.index
                }
            }).then(e => {
                this.coupon_list = e.data;
                // this.tempList = this.cloneData(e.data);
                // this.splitData();
                if (this.page_id === 0) {
                    let storage = this.$storage.getStorageSync('INDEX_MALL');
                    storage.home_pages[this.index].list = e.data;
                    this.$storage.setStorageSync('INDEX_MALL', storage);
                }
            })
        },
        cloneData(data) {
            return JSON.parse(JSON.stringify(data));
        },
        splitData() {
            if (!this.tempList.length) return;
            let item = this.tempList[0];
            this.coupon_list.push(item);
            this.tempList.splice(0, 1);
            if (this.tempList.length) {
                setTimeout(() => {
                    this.splitData();
                }, 300);
            }
        },
        route() {
            uni.navigateTo({
                url: '/pages/coupon/list/list'
            })
        }
    },
    mounted() {
        if (!this.coupon_req) {
            if (this.is_required) {
                this.loadData();
            } else {
                let storage = this.$storage.getStorageSync('INDEX_MALL');
                this.coupon_list = storage.home_pages[this.index].list;
                // this.tempList = this.cloneData(storage.home_pages[this.index].list);
                // this.splitData();
            }
        } else {
            this.coupon_list = this.list;
            // this.tempList = this.cloneData(this.list);
            // this.splitData();
        }
    },
}
